<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <b>{{ data.name }}</b>
          </div>
          <div class="modal-body" v-if="data.finished">
            <div class="chart-area">
              <b> </b>
              <br>
              <br>
              <canvas id="vote-chart"></canvas>
            </div>
          </div>
          <div class="modal-body" v-else>
            <div class="user-count">
              <fa-icon icon="user"/>
                : <b>{{ users }}</b>
            </div>
            <div class="chart-area">
              <b> </b>
              <br>
              <br>
              <canvas id="vote-chart"></canvas>
            </div>
            <div class="chart-area">
              <b>  </b>
              <br>
              <br>
              <canvas id="time-chart"></canvas>
            </div>
            <div class="chart-area">
              <b>  </b>
              <br>
              <br>
              <canvas id="gender-chart"></canvas>
            </div>
          </div>
          <button class="modal-default-button" @click="showBlocks">  </button>
          <br>
          <br>
          <button class="modal-default-button" @click="$emit('close')"></button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'vote-modal',
  props: ['data'],
  data () {
    return {
      voteData: null,
      resultChart: null,
      voteChart: null,
      timeChart: null,
      genderChart: null
    }
  },
  computed: {
    users () {
      try {
        return this.voteData['users'] || 0
      } catch (e) {
        return 0
      }
    }
  },
  created () {
    /*      */
    this.getVoteDetailData()
  },
  methods: {
    /**
     * @description    
     * ( ,   ,   )
     */
    getVoteDetailData () {
      if (!this.data.code) {
        console.log('  .')
        return
      }

      /* GET /info/vote/detail */
      this.$http.get('/info/vote/detail/' + this.data.code, {
        //   ()
        headers: { Authorization: 'Bearer ' + this.$store.state.token }
      }).then(r => {
        this.voteData = r.data.vote

        //      
        if (this.voteData.finished) {
          this.getVoteResult().then(r => {
            const d = r.data.result
            if (d) {
              this.createVoteResultChart(d)
            } else {
              alert('    .  ')
            }
          }).catch(e => {
            const code = e.response.data.code
            if (code === 2) {
              alert('  .  ')
              this.$store.commit('LOGOUT')
              this.$router.push({ path: '/' })
            } else if (code === 6) {
              alert('      .')
            } else if (code === 7) {
              alert('   .')
            } else {
              alert('    .')
            }
          })
        } else {
          //       
          this.createVoteDataChart()
          this.createTimeDataChart()
          this.createGenderDataChart()
        }
      }).catch(e => {
        const code = e.response.data.code
        if (code === 2) {
          alert('  .  ')
          this.$store.commit('LOGOUT')
          this.$router.push({ path: '/' })
        } else if (code === 6) {
          alert('      .')
        } else {
          alert('    .')
        }
      })
    },
    /**
     * @description     Promise
     */
    getVoteResult () {
      return this.$http.get('/info/vote/result/' + this.data.code, {
        headers: { Authorization: 'Bearer ' + this.$store.state.token }
      })
    },
    /**
     * @description    
     */
    createVoteResultChart (data) {
      const ctx = document.getElementById('vote-chart')

      let labels = []
      let datas = []
      data.forEach(d => {
        labels.push(d.item)
        datas.push(d.count)
      })

      this.resultChart = new this.$chart(ctx, {
        type: 'pie',
        data: {
          labels: labels,
          datasets: [
            {
              data: datas,
              backgroundColor: ['#f44336',
                '#1e88e5',
                '#ffeb3b',
                '#43a047',
                '#cddc39',
                '#8e24aa',
                '#ff4081',
                '#009688',
                '#ffca28',
                '#29b6f6'] //  
            }
          ]
        },
        options: {
          responsive: true
        }
      })
    },
    /**
     * @description    (  )
     */
    createVoteDataChart () {
      const ctx = document.getElementById('vote-chart')

      const labels = this.voteData.vote.map(d => d.item)
      const data = this.voteData.vote.map(d => d.count)

      this.voteChart = new this.$chart(ctx, {
        type: 'pie',
        data: {
          labels: labels,
          datasets: [
            {
              data: data,
              backgroundColor: ['#f44336',
                '#1e88e5',
                '#ffeb3b',
                '#43a047',
                '#cddc39',
                '#8e24aa',
                '#ff4081',
                '#009688',
                '#ffca28',
                '#29b6f6'] //  
            }
          ]
        },
        options: {
          responsive: true
        }
      })
    },
    /**
     * @description   
     */
    createTimeDataChart () {
      const ctx = document.getElementById('time-chart')

      const labels = this.voteData.time.map(d => d.hour)
      const data = this.voteData.time.map(d => d.count)

      this.timeChart = new this.$chart(ctx, {
        type: 'bar',
        data: {
          labels: labels,
          datasets: [
            {
              label: ' ',
              data: data,
              backgroundColor: '#00afa8'
            }
          ]
        },
        options: {
          responsive: true,
          scales: {
            yAxes: [
              {
                scaleLabel: {
                  display: true,
                  labelString: ' '
                },
                ticks: {
                  beginAtZero: true,
                  steps: 1,
                  callback (value) {
                    return value + ''
                  }
                }
              }
            ],
            xAxes: [
              {
                scaleLabel: {
                  display: true,
                  labelString: '(Hour)'
                },
                ticks: {
                  beginAtZero: true,
                  callback (value) {
                    return value + ''
                  }
                }
              }
            ]
          }
        }
      })
    },
    /**
     * @description   
     */
    createGenderDataChart () {
      const ctx = document.getElementById('gender-chart')

      const labels = this.voteData.gender.map(d => {
        if (d.gender === 0) {
          return ''
        } else if (d.gender === 1) {
          return ''
        } else {
          return ''
        }
      })
      const colors = this.voteData.gender.map(d => {
        if (d.gender === 0) {
          return '#36a2eb'
        } else if (d.gender === 1) {
          return '#ff6384'
        } else {
          return '#ffcd56'
        }
      })
      const data = this.voteData.gender.map(d => d.count)

      this.genderChart = new this.$chart(ctx, {
        type: 'doughnut',
        data: {
          labels: labels,
          datasets: [
            {
              data: data,
              backgroundColor: colors
            }
          ]
        },
        options: {
          responsive: true
        }
      })
    },
    /**
     * @description     
     * : /blocks?code=***
     */
    showBlocks () {
      this.$router.push({
        path: 'blocks',
        query: {
          code: this.data.code
        }
      })
    }
  }
}
</script>

<style lang="scss">

.chart-area {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, .3);
  margin: 10px 0;

  canvas {
    width: 100%;
  }
}

</style>
